import React, { useState } from 'react';
import axios from 'axios';
import './styles/Contact.css';
import { useFormik } from 'formik';

const validate = values => {
    const errors = {};
    if (!values.firstName) {
        errors.firstName = 'pole wymagane';
    } else if (values.firstName.length > 15) {
        errors.firstName = 'za dużo liter';
    }

    if (!values.lastName) {
        errors.lastName = 'pole wymagane';
    } else if (values.lastName.length > 20) {
        errors.lastName = 'za dużo liter';
    }

    if (!values.email) {
        errors.email = 'email wymagany';
    } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
        errors.email = 'niepoprawny adres email';
    }
    if (!values.message) {
        errors.message = 'Chcesz wysłać bez wiadomości?';
    } else if (values.message.length < 10) {
        errors.message = 'napisz coś';
    }

    return errors;
}

const Contact = () => {

    const formik = useFormik({
        initialValues: {
            firstName: '',
            lastName: '',
            email: '',
            message: ''
        },
        validate,
        onSubmit: values => {
            axios({
                method: "POST",
                url: process.env.REACT_APP_MY_EMAIL,
                data: values
            })
                .then(response => {
                    formik.setSubmitting(false);
                    formik.resetForm();
                    handleServerResponse(true, "Twoja wiadomość została wysłana");
                })
                .catch(error => {
                    formik.setSubmitting(false);
                    handleServerResponse(false, error.response.data.error);
                });
        },
    });
    //elemnty formspree
    const [serverState, setServerState] = useState();
    const handleServerResponse = (ok, msg) => {
        setServerState({ ok, msg });
    };
    return (
        <div className="contactMain">
            <h3>Bądźmy w kontakcie</h3>
            <form onSubmit={formik.handleSubmit}>

                <label htmlFor="firstName">Imię</label>
                <input
                    id="firstName"
                    name="firstName"
                    type="text"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.firstName}
                />
                {formik.touched.firstName && formik.errors.firstName ? (
                    <div>{formik.errors.firstName}</div>
                ) : null}


                <label htmlFor="lastName">Nazwisko</label>
                <input
                    id="lastName"
                    name="lastName"
                    type="text"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.lastName}
                />
                {formik.touched.lastName && formik.errors.lastName ? (
                    <div>{formik.errors.lastName}</div>
                ) : null}


                <label htmlFor="email">Adres email</label>
                <input
                    id="email"
                    name="email"
                    type="email"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.email}
                />
                {formik.touched.email && formik.errors.email ? (
                    <div>{formik.errors.email}</div>
                ) : null}

                <label htmlFor="message">Wiadomość</label>
                <textarea
                    id="message"
                    name="message"
                    type="textarea"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.message}
                />
                {formik.touched.message && formik.errors.message ? (
                    <div>{formik.errors.message}</div>
                ) : null}
                <button type="submit" >Wyślij</button>
                {serverState && (
                    <p className={!serverState.ok ? "errorMsg" : ""}>
                        {serverState.msg}
                    </p>
                )}
            </form>

        </div>
    );
}
export default Contact;